<template>
  <ListContainer :title="`成人小說 ${type}`" @onClick="handleToMore('/video')">
    <NovelList :dataSource="fakeNovel" />
  </ListContainer>
</template>
<script>
import ListContainer from "@/components/ListContainer";
import NovelList from "@/components/NovelList";
import { novels } from "../../fakeData";
import mobileMixin from "../../mixin/mobileMixin";

const fakeNovel = novels.slice(0, 15).map((item) => ({
  ...item,
  path: "/novel",
}));

export default {
  mixins: [mobileMixin],
  components: {
    ListContainer,
    NovelList,
  },
  data() {
    return {
      fakeNovel,
    };
  },
  computed: {
    type() {
      return this.$route.params.type || "";
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
  methods: {
    handleAddAds() {
      this.fakeNovel = this.addAds(this.fakeNovel, 3);
    },
    handleRemoveAds() {
      this.fakeNovel = this.removeAds(this.fakeNovel);
    },
  },
};
</script>
